@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@300&500&display=swap');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Epilogue', sans-serif;
}

.create-team-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    width: 99.5vw;
    height: 100vh;
    font-size: 30px;
}

.register-form {
    display: flex;
    flex-direction: column;
    width: 30vw;
}

.input-title {
    margin-bottom: 5px;
    margin-top: 25px;
}

.input-box {
    height: 30px;
}

.button-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
}

.submit-button {
    background: white;
    color: black;
    border-radius: 2.5vh;
    margin-top: 2.25vh;
    position: center;
    width: 20vw;
    padding: 1vh;
    font-size: 2vh;
    height: auto;
}

.select-input {
    height: 30px;
    border-radius: 10px;
}

